import { Component, Prop, State, Event, EventEmitter, h, Host } from '@stencil/core';

export interface PopoverItem {
  id: string;
  label: string;
  type?: 'portfolio' | 'region' | 'property' | 'header';
  selected?: boolean;
}

@Component({
  tag: 'bh-popover',
  styleUrl: 'bh-popover.css',
  shadow: true,
})
export class BhPopover {
  /**
   * Title displayed in the header
   */
  @Prop() popoverTitle: string = 'Brand Name';

  /**
   * Text for the portfolio link
   */
  @Prop() viewPortfolioText: string = 'view portfolio';

  /**
   * Placeholder text for search input
   */
  @Prop() searchPlaceholder: string = 'Search by name or region';

  /**
   * Width of the popover
   */
  @Prop() width: string = '240px';

  /**
   * Whether to show search input
   */
  @Prop() showSearch: boolean = true;

  /**
   * Whether to show avatar section
   */
  @Prop() showAvatar: boolean = true;

  /**
   * Avatar image source URL
   */
  @Prop() avatarSrc?: string;

  /**
   * Menu items array (JSON string or array)
   */
  @Prop() items: PopoverItem[] | string = [];

  @State() searchTerm: string = '';

  /**
   * Event emitted when a menu item is selected
   */
  @Event() bhItemSelect!: EventEmitter<PopoverItem>;

  /**
   * Event emitted when portfolio link is clicked
   */
  @Event() bhPortfolioClick!: EventEmitter<void>;

  /**
   * Event emitted when search input changes
   */
  @Event() bhSearchChange!: EventEmitter<string>;

  private get parsedItems(): PopoverItem[] {
    if (typeof this.items === 'string') {
      try {
        return JSON.parse(this.items);
      } catch {
        return [];
      }
    }
    return this.items || [];
  }

  private handleItemClick = (item: PopoverItem) => {
    if (item.type !== 'header') {
      this.bhItemSelect.emit(item);
    }
  };

  private handlePortfolioClick = () => {
    this.bhPortfolioClick.emit();
  };

  private handleSearchInput = (event: Event) => {
    const target = event.target as HTMLInputElement;
    this.searchTerm = target.value;
    this.bhSearchChange.emit(this.searchTerm);
  };

  private getItemClasses(item: PopoverItem): Record<string, boolean> {
    return {
      'menu-item': true,
      'selected': !!item.selected,
      'menu-item-portfolio': item.type === 'portfolio',
      'menu-item-region': item.type === 'region',
      'menu-item-property': item.type === 'property',
    };
  }

  render() {
    const items = this.parsedItems;

    return (
      <Host>
        <div class="popover" style={{ width: this.width }}>
          <div class="popover-content">
            <div class="popover-menu">
              {/* Header with Brand/Avatar */}
              {this.showAvatar && (
                <div class="popover-header">
                  <div class="avatar-group">
                    <div class="avatar">
                      {this.avatarSrc ? (
                        <img src={this.avatarSrc} alt={this.popoverTitle} class="avatar-image" />
                      ) : (
                        <slot name="avatar" />
                      )}
                    </div>
                    <div class="brand-info">
                      <div class="brand-name">{this.popoverTitle}</div>
                      {this.viewPortfolioText && (
                        <button
                          class="portfolio-link"
                          onClick={this.handlePortfolioClick}
                          type="button"
                        >
                          {this.viewPortfolioText}
                        </button>
                      )}
                    </div>
                  </div>
                </div>
              )}

              {/* Search Input */}
              {this.showSearch && (
                <div class="search-container">
                  <div class="search-input">
                    <div class="search-content">
                      <span class="material-symbols-outlined search-icon">search</span>
                      <input
                        type="text"
                        placeholder={this.searchPlaceholder}
                        value={this.searchTerm}
                        onInput={this.handleSearchInput}
                        class="search-field"
                      />
                    </div>
                  </div>
                </div>
              )}

              {/* Menu Items */}
              <div class="menu-items">
                {items.map((item) => (
                  <div key={item.id}>
                    {/* Header Item */}
                    {item.type === 'header' && (
                      <div class="menu-header">{item.label}</div>
                    )}

                    {/* Regular Item */}
                    {item.type !== 'header' && (
                      <div class="menu-item-container">
                        <button
                          class={this.getItemClasses(item)}
                          onClick={() => this.handleItemClick(item)}
                          type="button"
                        >
                          {item.label}
                        </button>
                      </div>
                    )}
                  </div>
                ))}
              </div>
            </div>
          </div>
        </div>
      </Host>
    );
  }
}
